.info-user__name {
    margin: 0;
    width: 294px;
    font-style: normal;
    font-weight: 500;
    font-size: 42px;
    line-height: 48px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

@media screen and (max-width: 600px) {
    .info-user__name {
        width: 189px;
        font-size: 27px;
        line-height: 33px;
        margin-left: 50px;

    }
}
