.popup__field {
    padding: 0 0 13px;
    border: none;
    border-bottom: 1px solid rgba(0, 0, 0, .2);
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    margin: 0;
}

.popup__field:focus {
    outline: none;
}

.popup__error-user {
    border-bottom-color: #FF0000;
}